export default {
	name: 'ClockIn',
	data() {
		return {
			date: '',
			rotate: false,
			onTouch: false
		}
	},
	methods: {

		touchstart() {
			this.onTouch = true
		},
		touchend() {
			this.onTouch = false
			this.rotate = !this.rotate
			this.$emit('onTap')
		},
		//获取当前日期
		mDate() {
			let date = new Date(Date.now()).toString()
			let arr = date.split(' ')
			let str = arr[0] + ' ' + arr[1] + ' ' + arr[2]
			return str
		}
	},
	computed: {
		_rotate() {
			//处理值
			return String(this.rotate) !== 'false'
		}
	},
	created() {
		// 获取日期
		this.date = this.mDate()
	}
}
